import $axios from 'axios';
import $config from '@/config';
let baseURL = process.env.NODE_ENV === 'development' ? $config.baseURL.dev : $config.baseURL.pro
import {token} from '../libs/util';
import qs from 'qs'

let service = $axios.create({
    baseURL,
    timeout:$config.timeout * 1000
})

//
service.interceptors.request.use(config => {
    if(config.method == 'get') {
        Reflect.set(config.params,'appkey',$config.appkey);
        token("token") && Reflect.set(config.params,'tokenString',token("token"));
    }

    if(config.method == 'post') {
        Reflect.set(config.data,'appkey',$config.appkey);
        token("token") && Reflect.set(config.data,'tokenString',token("token"));
        Reflect.set(config,'data',qs.stringify(Reflect.get(config,'data')));
    }
    

    return config
},error => {
    return Promise.reject(error)
})

//

service.interceptors.response.use(response => {
    return Promise.resolve(response.data)
},error => {
    return Promise.reject(error)
})

export default service;