import {get,post} from './common';
/**
 * @param {String} url 
 * @param {Object} params 
 * @returns {Promise} 
 * @description GET
 */
export let getRequest = (url = '',params = {}) => {
    return new Promise((resolve,reject) => {
        get(url,params).then((result) => {
            resolve(result)
        }).catch((error) => {
            reject(error)
        });
    })
}
/**
 * @param  {String} url 
 * @param {Object} data 
 * @returns {Promise} 
 * @description POST
 */
export let postRequest = (url = '',data = {}) => {
    return new Promise((resolve,reject) => {
        post(url,data).then((result) => {
            resolve(result)
        }).catch((error) => {
            reject(error)
        });
    })
}
